<template>
    <div class="container">
      <div class="info">
        <div class="filtr">
          <h2 class="filtr__logo">Сортировка</h2>
          <div class="filtr__item">
            <li class="item" >
              <button
              v-bind:class="{active: activeLink === 1}" 
              @click="sortUserByName()" 
               class="filtr__item-btn" id='item1'
                v-on:click.prevent="activeLink = 1"
                >ИМЯ</button>
            </li>
            <li class="item" >
              <button 
              v-bind:class="{active: activeLink === 2}"
              @click="sortUserByStar()"
               class="filtr__item-btn" id='item2' 
               v-on:click.prevent="activeLink = 2">
               ЗВЕЗДЫ
              </button>
            </li>
            <li class="item" >
              <button 
              v-bind:class="{active: activeLink === 3}"
              @click="sortUserByDate() "
               class="filtr__item-btn" id='item3'
                v-on:click.prevent="activeLink = 3">
                ДАТА
              </button>
            </li>
          </div>
        </div>
        <div class="info__item" v-for="item in repos" :key="item">
          <div class="info__item-left">
            <span class="info__item-left-title">{{ item.name }}</span>
            <p class="info__item-left-text">
              Кол-во звёзд:{{ item.forks_count }}
            </p>
            <p class="info__item-left-text">
              Дата добавления: {{ item.created_at.slice(0,10) }}
            </p>
          </div>
          <div class="info__item-right">
            <a :href="item.clone_url" class="info__item-right-btn">ПОСЕТИТЬ</a>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { mapState } from "vuex";
  export default {
    computed: {
      ...mapState(["repos"]),
    },
    data() {
    return {
      activeLink: undefined

}
},
    methods: {
      sortUserByStar() {
        return this.repos.sort((a, b) => b.forks_count - a.forks_count);
      },
      sortUserByName() {
        return this.repos.sort((a, b) => a.name.localeCompare(b.name));
      },
      sortUserByDate() {
        return this.repos.sort((a, b) => b.created_at.localeCompare(a.created_at));
      },
      changeColor(){
        return 
      },
    },
    
  };
  </script>
  