<template>
    <div class="container">

        <div class="search">
          <input type="text" 
          placeholder="введите имя пользователя" 
          class="search__input"
          v-model="getUserId"
          @keydown.enter="getUser(getUserId)"
          >
          <button class="serach__button" @click="getUser(getUserId) , resetInp" >НАЙТИ</button>
        </div>
        
    </div>
</template>

<script>
import { mapState, mapActions , mapMutations  } from 'vuex';

    export default {
        computed:{
            ...mapState(['user']),
            getUserId:{
                get() {
                    return this.user
                },
                set(value){
                    this.setUser(value)
                },
            },
            resetInp(){
                this.getUserId = ''
            }
        },
        methods:{
            ...mapMutations(['setUser']),
            ...mapActions(['getUser'])
        }
    }
</script>
